<template>
    <div class="container">
        <p>Login</p>
        <hr>
        <div class="input-form">
            <input type="text" v-model="username" placeholder="username">
            <input type="password" v-model="password" placeholder="password">
        </div>
        <p class="error-info">{{ message }}</p>
    </div>
    <div class="button-submit">
        <button @click="login()" :class="{active: loggedBtn}" >LOG IN</button>
        <Loader v-if="loaderBool" class="loader"/>
    </div>
</template>

<script>
import Loader from './Loader.vue'

export default {
    name: 'LoginPage',
    components: {
        Loader,
    },
    data() {
        return {
            loggedBtn: false,
            loaderBool: false,
            message: '',
            username: '',
            password: '',
        }
    },
    methods: {
        login() {
            this.loaderBool = true
            const url = "http://localhost:3305/api/user/login"
            fetch(url, {
                method: 'POST',
                headers: {
                    "Content-Type": "application/json"
                },
                body: JSON.stringify({username: this.username, password: this.password})
            })
            .then(res => this.handleError(res))
            .then(res => res.json())
            .then(data => {
                if(data.token) {
                    this.loggedBtn = true
                    this.loaderBool = false
                    localStorage.setItem("username", this.username)
                    localStorage.setItem("token", data.token)
                    this.$router.push({name: 'home'})
                    console.log(data.message)
                } else {
                    this.message = data.message
                }
            })
        },

        handleError(response) {
            if(!response.ok) {
                throw Error(response.statusText)
            }
            return response
        },
    }
}
</script>

<style scoped>

    .error-info {
        text-align: center;
        font-size: 20px;
        margin: 0 0 0 0;

        animation: show 1.5s;
    }

    .loader {
        animation: show 0.9s;
    }
    @keyframes show {
        0% {
            opacity: 0;
        }
        100%{
            opacity: 1;
        }
    }
    .container {
        background-color: #DAD7CD;
        margin: 500px auto;
        padding: 10px;
        border-radius: 10px;
        border-bottom-left-radius: 0px;
        border-bottom-right-radius: 0px;
        width: 300px;
    }

    .button-submit {
        margin: -510px auto;
        padding: 10px;
        width: 320px;
        height: 200px;

        display: flex;
        align-items: center;
        flex-direction: column;
    }

button,
button:active,
button:focus  {
  border:none;
}

.button-submit > .active {
    background: rgb(23, 163, 37);
    color: #DAD7CD;
}

.button-submit > button {
    width: 320px;
    height: 70px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border: none;
    padding: 10px;
    outline: none;

    color: #b5b5b5;
    font-size: 20px;

    background: linear-gradient(to left, rgb(22, 22, 22) 50%, rgb(137, 40, 2) 50%) right;
    background-size: 200%;
    transition: .3s ease-out;
}

.button-submit > button:hover {
    background-position: left;
    color:#DAD7CD;
}

    .input-form {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .input-form > input {
        margin-bottom: 20px;
        width: 280px;

        outline: none;
        padding: 5px;
    }

    .container > p:first-child {
        font-size: 35px;
        text-align: center;
    }
</style>