<template>
    <NavMenu></NavMenu>
    <div class="container">
        <div class="user-info">
            <p>Name: {{ username }}</p>
            <div v-if="isAvatar">
                <p>Avatar: <img class="check-mark" src="../assets/check-mark-true.png"></p>
            </div>
            <div v-else>
                <p>Avatar: <img class="check-mark" src="../assets/check-mark-false.png"></p>
            </div>
        </div>
        <hr/>
        <div class="profile-image">
            <p>Choose a profile image</p>
            <input @change="uploadFile" class="input-files" type="file" accept="image/png, image/jpeg"/>
            <p>{{ message }}</p>
        </div>   
    </div>
    <img :src="avatar">
</template>

<script>
import NavMenu from './NavMenu.vue';
export default {
    name: 'profile-page',
    components: {
        NavMenu,
    },
    data() {
        return {
            username: 'Alex',
            isAvatar: false,
            message: '',
            avatar: ''
        }
    },
    created() {
        this.username = localStorage.getItem("username")
        this.getProfileAvatar()
    },
    methods: {
        handleError(response) {
            if(!response.ok) {
                throw Error(response.statusText)
            }
            return response
        },
        uploadFile() {
            const url = "http://localhost:3305/api/files/loadfile"
            const form = new FormData()
            let files = Array.from(event.target.files)
            form.append("file", files[0])
            fetch(url, {
                method: 'POST',
                headers: {
                    'Authorization':'Bearer ' + localStorage.getItem("token")
                },
                body: form
            })
            .then(res => this.handleError(res))
            .then(res => res.json())
            .then(data => {
                if(data.message) {
                    console.log(data.message)
                    this.message = data.message
                    this.isAvatar = true
                    localStorage.setItem("pathAvatar", data.path)
                    this.getProfileAvatar()
                } else {
                    this.message = "You are already have avatar"
                }
            })
        },
        getProfileAvatar() {
            if(localStorage.getItem("pathAvatar")) {
                const url = `http://localhost:3305/api/files/getfile/?path=${localStorage.getItem("pathAvatar")}`
                fetch(url, {
                    method: 'GET',
                    headers: {
                        'Authorization':'Bearer ' + localStorage.getItem("token")
                    },
                })
                .then(res => this.handleError(res))
                .then(res => res.json())
                .then(async data => {
                    const blob = await data.blob() 
                    this.avatar = window.URL.createObjectURL(blob)
                    console.log(this.avatar)
                })
            } else {
                console.log("No path data")
            }
            
        }
    }
}
</script>

<style scoped>
    .input-files {
        color: transparent;
        outline: none;
        margin-left: 130px;
    }
    hr {
        width: 100%;
    }

    .profile-image {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .user-info {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }

    .check-mark {
        height: 25px;
        width: 25px;
        vertical-align: middle;
    }

    .container {
        margin: 300px auto;
        background-color: #DAD7CD;
        padding: 5px;
        border-radius: 10px;
        width: 250px;
        height: 250px;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
</style>